.promo__text {
  position: relative;

  margin: 0;
  padding: 0;

  text-align: center;

  @include font(400, 16px, 20px, $font-family--alt, $color-text--rev);
}

@media (min-width: $tablet-width) {
  .promo {
    padding: 72px 120px 68px 59.75%;

    background-image: url("../img/gift-photo-tablet@1x.jpg");
    background-size: 708px 100%;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }

    &::before {
      top: 64px;
      left: 90px;
    }
  }


  .promo__text {
    padding: 0;

    text-align: left;

    &::before {
      left: 90px;
    }
  }
}

@media (min-width: $desktop-width) {
  .promo {
    width: 245px;
    margin-left: auto;
    padding: 187px 40px 40px;

    background-image: url("../img/gift-photo-desktop@1x.jpg");
    background-size: 245px 100%;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }
  }

  .promo__text {
    padding: 0;

    text-align: center;
  }
}
